  hyphens: none;
}

/* Code blocks */

pre[class*="language-"] {
  padding: 1em;
  margin: 1.5em 0;
  overflow: auto;
}

:not(pre)>code[class*="language-"], pre[class*="language-"] {
  background: #272822;
}

/* Inline code */

:not(pre)>code[class*="language-"] {
  padding: .1em;
  border-radius: .3em;
  white-space: normal;
}

